.HomePost_HomePost__16vcN {
  cursor: pointer;
  position: relative;
  left: 0px;
  transition: 500ms cubic-bezier(0.075, 0.82, 0.165, 1);
}

.HomePost_HomePost__16vcN:hover {
  left: 15px;
  transition: 500ms cubic-bezier(0.075, 0.82, 0.165, 1);
  background-blend-mode: darken;
}
.HomePost_HomePost__16vcN {
  
  position: relative;
  background: hsla(204, 86%, 53%, 0.363);
  border-radius: 13px;
  box-shadow: 10px 0 100px 30px  hsla(204, 86%, 53%, 0.151);;
}

.HomePost_HomePost__16vcN h3 {
  font-weight: bolder;
  letter-spacing: 2px;
}

.HomePost_HomePost__16vcN time {
  position: absolute;
  bottom: -28px;
  right: -35px;
  background: hsl(48, 100%, 67%);
  border-radius: 15px;
  padding: 15px;
  margin-right: 10px;
}


